import React, { useState, useMemo } from 'react';
import {
    ComposedChart,
    CartesianGrid,
    XAxis,
    YAxis,
    Tooltip,
    Legend,
    Scatter,
    BarChart,
    Bar,
    Line
} from 'recharts';
import { linearRegression, linearRegressionLine } from 'simple-statistics';
import { ChartSuggest } from '../chart-suggest'
import { useResize } from 'utils/hooks'
const computeHistogram = require('compute-histogram')

const Suggester = ({ selectedChart, selectChart }) =>
    <ChartSuggest value={selectedChart} onSuburbSelected={selectChart} />

const Histogram = ({ plot, width }) => {
    const histogram = useMemo(() =>
        computeHistogram(
            plot.data
            .filter(item => !!item && !!item[plot.axis.keys[0]])
            .map(item => item[plot.axis.keys[0]])
        )
        .map(item => ({ x: item[0], y: item[1] })),
        [plot])

    return (
        <BarChart
            width={width}
            height={500}
            data={histogram}
            margin={{ top: 5, right: 130, left: 70, bottom: 5 }}>
            <CartesianGrid strokeDasharray="1 1" />
            <XAxis dataKey="x" type="number" />
            <Tooltip
                labelFormatter={(value) => `Bin #${value}`}
                formatter={(value) => [value, 'Total'] } />
            <Legend
                verticalAlign="top"
                wrapperStyle={{ lineHeight: '50px' }}
                formatter={() => `${plot.axis.names[0]} - Histogram`}
            />
            <Bar dataKey="y" fill={plot.color} />
        </BarChart>
    )
}


const ScatterPlot = ({ plot, width }) => {

    const [ regressionFrom, regressionTo ] = plot.axis.keys

    const result = useMemo(() => {
        const line = linearRegressionLine(
            linearRegression(plot.data.map(item => [item[regressionFrom], item[regressionTo] ]))
        )

        return plot.data
            .concat([{
                [regressionFrom]: plot.data[0][regressionFrom],
                regression: Math.trunc(line(plot.data[0][regressionFrom]))  }])
            .concat([{
                [regressionFrom]: plot.data[plot.data.length -1][regressionFrom],
                regression: Math.trunc(line(plot.data[plot.data.length -1][regressionFrom])) }])
    }, [regressionFrom, regressionTo, plot.data])

    return (
        <ComposedChart width={width} height={500} data={result}
            margin={{ top: 5, right: 130, left: 70, bottom: 5 }}>
            <CartesianGrid strokeDasharray="1 1" />
            <XAxis dataKey={regressionFrom} type="number" name={plot.axis.names[0]} unit="" />
            <YAxis name={plot.axis.names[1]} unit="" />
            <Tooltip
                labelFormatter={(value) => `${plot.axis.names[0]}: ${value}`}
                formatter={(value) => [value, plot.axis.names[1]] } />
            <Legend
                verticalAlign="top"
                wrapperStyle={{ lineHeight: '50px' }}
                formatter={(value) => value === 'regression' ? 'Regression line' : `${plot.axis.names[0]} - ${plot.axis.names[1]}`} />
            <Scatter dataKey={regressionTo} fill={plot.color} />
            <Line dataKey="regression" stroke="red"  />
        </ComposedChart>
    )
}

export const ChartByPair = ({ plots }) => {
    const [width] = useResize();
    const [ selectedChart, selectChart ] = useState('');

    const plot = useMemo(() => plots.filter(item => item.cell === selectedChart)[0],
        [selectedChart, plots]);

    const renderBody = (plot) =>
        plot.isDiagonal ?
            (<Histogram {...{ plot, width }} />) :
            (<ScatterPlot {...{ plot, width }} />)

    return (
        <>
            <Suggester {...{selectedChart, selectChart}} />
            {plot && renderBody(plot)}
        </>
    )
}
